<script>

export default {
    data() {
        let appFront = this.$kt.i18n.getAppFront();
        return {
            appFront,
            statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
            navHeight: 48,
            // #ifdef MP-WEIXIN
            menuButton: wx.getMenuButtonBoundingClientRect(),
            // #endif
            screenTop: 0,
            ovoUserSelf: {},
            balanceSelfMap: {},
        };
    },
    created() {
        this.ovoUserSelf = this.$ovo.getSelf();
        this.balanceSelfMap = this.$system.getBalanceSelfMap();
        uni.$on("changeOvoSelfInfo",()=>{
            this.ovoUserSelf = this.$ovo.getSelf();
        });
        uni.$on("changeBalanceSelfMap",()=>{
            this.balanceSelfMap = this.$system.getBalanceSelfMap();
        });
    },
    mounted() {
        this.navHeight = 44;
        // #ifdef MP-WEIXIN
        this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
        // #endif

    },
    methods: {
        onScroll(res) {
            this.screenTop = this.$kt.util.pxToRpx(res.target.scrollTop);
        }
    },
}
</script>

<style lang="scss" scoped>
.eye,
.eye2 {
    position: relative;
    // 文字间距
    letter-spacing: 2rpx;
}

.eye:active {
    opacity: .5;
}

.box {
    padding: 30rpx;
    box-sizing: border-box;

    .box-in {
        padding: 20rpx;
        box-sizing: border-box;
        background-color: #ffffff;
        border-radius: 20rpx;
    }
}

.box-in-other {
    padding: 20rpx;
    box-sizing: border-box;
    border-radius: 20rpx;
    margin-top: 20rpx;
    color: #333;

