                display: inline-block;
                font-size: 30rpx;
                // 文字间距
                letter-spacing: 2rpx;
                line-height: 70rpx;
                margin-left: 50rpx;
            }
        }
    }
}

.box-in-other-line-item:active {
    opacity: .5;
}

.status-bar {
    background-color: #fff;
}

.nav-bar {
    position: relative;
    background-color: #fff;

    .nav-bar-icon {
        position: absolute;
        width: 45rpx;
        height: 45rpx;
    }

    .nav-bar-icon:active {
        transform: scale(0.9);
    }

    .nav-bar-title {
        position: absolute;
        left: 20rpx;
        font-size: 42rpx;
        top: 50%;
        transform: translateY(-50%);
        // 文字间距
        // letter-spacing: 5rpx;
    }

    .o-money-box {
        position: absolute;
        left: 50rpx;
        font-size: 28rpx;
        top: 50%;
        transform: translateY(-50%);
        color: #333333;
        font-weight: bold;
        background-color: #f0f0f0;
        padding: 5rpx 10rpx;
        border-radius: 10rpx;
    }
}

.t {

    padding-bottom: 30rpx;

    .tt {
        display: inline-block;
        font-size: 32rpx;
        margin-right: 30rpx;
        vertical-align: bottom;
    }

    .tt-no-active:active {
        opacity: 0.5;
    }

    .tt-active {
        font-weight: bold;
    }


}

.content-view {
    padding: 20rpx;
    box-sizing: border-box;
    background-color: #ffffff;
}

.grid-item2 {
    margin-bottom: 20rpx;
}

.grid-item {
    position: relative;
    padding: 20rpx 0 0rpx 0;
    box-sizing: border-box;
    text-align: center;
    width: 95%;
    border-radius: 10rpx;

    .grid-item-number {
        font-size: 32rpx;
        margin-bottom: 7rpx;
        color: #333333;
    }

    .grid-item-title {
        font-size: 26rpx;
        color: #aaaaaa;
    }

    .grid-item-title2 {
        font-size: 26rpx;
        color: #333333;
    }

    .grid-item-icon {
        width: 60rpx;
        height: 60rpx;
        margin-bottom: 10rpx;
    }

    .grid-item-line {
        position: absolute;
        height: 30rpx;
        width: 10rpx;
        border-radius: 10rpx;
        background-color: #dcdfe6;
        top: calc(50% + 15rpx);
        transform: translateY(-50%);

    }

}

.o-money-box:active {
    .o-money-box-num {
        opacity: .6;
    }
}
</style>
